import React, {Component} from 'react'

import {Provider} from 'react-redux'

import {Route, Redirect, Switch, } from 'react-router-dom'

import {ConnectedRouter} from 'react-router-redux'

import configureStore, {history} from './configStore.web'

import styled from 'styled-components'

require('./commom/GlobalValue.web');

import {
    AsyncHome,

    AsyncAccount,

    AsyncSys,
} from './pages/index.web'

export let store;

const NoMatch = ({ location }) => (
    <div>
      <h3>
        No match for <code>{location.pathname}</code>
      </h3>
    </div>
  );

class Root extends Component {
    render() {
        return (
            <RootDiv id="rootDiv">
                <Switch>
                    <Route exact path="/" render={() => (
                        <Redirect to="/Home"/>
                    )}/>
                    <Route path="/Home" component={AsyncHome}/>
                    <Route path="/Account" component={AsyncAccount}/>
                    <Route path="/Sys" component={AsyncSys}/>

                    {/*添加一个404*/}
                    <Route component={NoMatch} />
                </Switch>
            </RootDiv>
        )
    }
}

export default class extends Component {
    constructor() {
        super(...arguments)
    }

    render() {
        store = configureStore()
        return (
            <Provider store={store}>
                <ConnectedRouter history={history} basename="/tempApp">
                    <Root/>
                </ConnectedRouter>
            </Provider>
        )
    }
}

const RootDiv = styled.div`
  height: 100%;
  width: 100%;
`;



