//@flow

import {
    Request,
    Success,
    Failed,
    Cancle,
    HasMoreData,
} from './PullDownRefreshListActionTypes'

import initState from './PullDownRefreshListInitState'

export default function (state: Object = initState, action: Object) {
    switch (action.type) {
        case Request:
            if (action.isRefresh) {
                return state
                    .set('isRefreshing', true)
                    .set('isLoading', false)
                    .set('curPage', 1)
                    .set('requestId', action.requestId)
            } else {
                return state
                    .set('isRefreshing', false)
                    .set('isLoading', true)
                    .set('curPage', action.page)
                    .set('requestId', action.requestId)
            }
        case Success:
            if (action.isRefresh) {
                return state
                    .set('isRefreshing', false)
                    .update('list', (list) => list.slice(0,0).concat(action.data))
            } else {
                return state
                    .set('isLoading', false)
                    .update('list', (list) => list.concat(action.data))
            }
        case Failed:
            return state
                .set('isRefreshing', false)
                .set('isLoading', false)
        case Cancle:
            return state
                .remove('isRefreshing')
                .remove('isLoading')
                .remove('hasMore')
                .remove('list')
                .remove('curPage')
        case HasMoreData:
            return state.set('hasMore', action.hasMore)
    }

    return state;
}


