import {store} from '../App.web'
import {history} from '../configStore.web'
import {Modal} from 'antd-mobile'
import {setCookie, getCookie, randomChar} from './CookieUtil.web'

const getUserId = () => {
    return new Promise((resolve, reject) => {
        const state = store.getState();
        const content = state.tokenReducer.content;
        if(content) {
            resolve(content);
            return
        }
        reject();
        //需要用户登录
        showReloginAlert('请先登录')
    })
}

const getJwtToekn = () => {
    return new Promise((resolve, reject) => {
        const state = store.getState()
        const userInfo = state.tokenReducer.userInfo
        if(userInfo) {
            resolve(userInfo.jwtToken)
        } else {
            reject()
            //需要用户登录
            showReloginAlert('请先登录')
        }
    })
}

const getUUID = () => {
    var uuid = getCookie("cookieUuid");
    if(uuid !='' && uuid != undefined){
        return uuid;
    }
    var str = randomChar();
    setCookie("cookieUuid",str,10950)
    return str;
}

function showReloginAlert() {
    Modal.alert('温馨提示', '请先登录', [
        { text: '取消', onPress: () => {}},
        { text: '确认', onPress: () => {history.replace('/Sys/')}, style: 'default'}
    ])
}

function handlerServiceError(model) {
    if(model.code == 700) {
        //登录过期、token失效
        Modal.alert('温馨提示', '请登录！', [
            { text: '取消', onPress: () => {}},
            { text: '确认', onPress: () => {history.replace('/Sys/')}, style: 'default'}
        ])
    }
    throw new Error(model.msg || '未定义错误信息')
}

