//@flow

import {Record} from 'immutable'

const PullDownRefreshListInitState = Record({
    list: [],
    isRefreshing: false,    //以之前的请求为准，比如正在下拉刷新时，后面的下拉请求都会被忽略
    isLoading: false,   //正在上拉加载更多时，上拉请求会被忽略
    hasMore: false,
    curPage: 1,
    requestId: 0,   //以后面的请求为准，如正在下拉刷新，此次又去上拉刷新，就会忽略下拉刷新的结果
})

const initState = new PullDownRefreshListInitState()

export default initState


