import React,{Component} from 'react'

import {TabBar} from 'antd-mobile'


class MainTab extends Component {
    render() {
        const {history, match, location} = this.props;

        return (
            <TabBar
                unselectedTintColor="#9a9a9a"
                tintColor="#11b6f8"
                barTintColor="white"
            >
                <TabBar.Item
                    title="首页"
                    key="Home"
                    icon={<div style={{
                        width: '0.42rem',
                        height: '0.4rem',
                        background: `url(${require('./resources/homeHide.png')}) center center /  0.42rem 0.4rem no-repeat` }}
                    />
                    }
                    selectedIcon={<div style={{
                        width: '0.42rem',
                        height: '0.4rem',
                        background: `url(${require('./resources/homeShow.png')}) center center /  0.42rem 0.4rem no-repeat` }}
                    />
                    }
                    onPress={() => {
                        history.push(`/Home`)
                    }}
                    selected={location.pathname === `/Home`}
                >
                </TabBar.Item>
                <TabBar.Item
                    title="账户"
                    key="Account"
                    icon={<div style={{
                        width: '0.36rem',
                        height: '0.42rem',
                        background: `url(${require('./resources/myHide.png')}) center center /  0.36rem 0.42rem no-repeat` }}
                    />
                    }
                    selectedIcon={<div style={{
                        width: '0.36rem',
                        height: '0.42rem',
                        background: `url(${require('./resources/myShow.png')}) center center /  0.36rem 0.42rem no-repeat` }}
                    />
                    }
                    onPress={() => {
                        history.push(`/Account`)
                    }}
                    selected={location.pathname === `/Account`}
                >
                </TabBar.Item>
            </TabBar>
        )
    }
}

export default MainTab


