//@flow

import {
    StartCD,
    CountCD,
    CancleCD,
} from './CDManangerActionType'

import {Map} from 'immutable'

import CDManagerInitData from './CDManagerInitState'
const initState = Map()

export default function cdManagerReducer(state: any = initState, action: Object) {
    switch (action.type) {
        case StartCD:
            const startDate = new Date()
            return updata(state, action)
                .setIn([action.cdKey, 'startTime'], startDate.getTime())
                .setIn([action.cdKey, 'totalSecond'], action.totalSecond)
                .setIn([action.cdKey, 'subSecond'], action.totalSecond)
                .setIn([action.cdKey, 'isInCd'], true)
        case CountCD:
            const curDate = new Date()
            const pastTime = curDate.getTime() - state.get(action.cdKey).startTime
            //每次取的总秒数减去递增的时间=剩下的秒数
            const subSecond = state.get(action.cdKey).totalSecond - (pastTime/1000) + 1
            console.log('countCD='+subSecond);
            if(subSecond > 0) {
                return updata(state, action)
                    .setIn([action.cdKey, 'subSecond'], Math.floor(subSecond))
                    .setIn([action.cdKey, 'isInCd'], true)
            }
        case CancleCD:
            return updata(state, action)
                .setIn([action.cdKey, 'isInCd'], false)
        default:
            return state

    }
}

function updata(state, action) {
    let newState = state
    const identifier = action.cdKey
    if ( !state.get(identifier) ) {   //不存在当前identifier,则给初始化
        newState = state.set(identifier, new CDManagerInitData())
    }
    return newState
}


