import { combineReducers } from 'redux'

import { reducer as formReducer } from 'redux-form'
import { routerReducer } from 'react-router-redux'

import TokenReducer from './reducers/TokenReducer'
import RefreshReducer from './component/refreshAble/RefreshReducer'
import PullDownRefreshListReducer from './component/PullDownRefreshList/PullDownRefreshListReducer'

import {UploadReducer} from "./component/upload";
import {CDManagerReducer} from './component/cdManager';

function createNamedWrapperReducer(reducerFunction, reducerName) {
    return (state, action) => {
        const {name} = action;
        const isInitializationCall = state === undefined;
        if (name !== reducerName && !isInitializationCall) return state;

        return reducerFunction(state, action);
    }
}

export default combineReducers({
    form: formReducer,
    router: routerReducer,
    tokenReducer: TokenReducer,
    refreshAble: RefreshReducer,
    RefreshUpAndDown: createNamedWrapperReducer(PullDownRefreshListReducer, 'RefreshUpAndDown'),
    upload:UploadReducer,
    cdManager: CDManagerReducer,
})


