import { createStore, combineReducers, applyMiddleware, compose } from 'redux'
import { routerMiddleware } from 'react-router-redux'
import {persistStore, autoRehydrate} from 'redux-persist'
import immutableTransform from 'redux-persist-transform-immutable'
import reducers from './reducers.web' // Or wherever you keep your reducers
import {TokenRecord} from './reducers/TokenReducer'
import thunk from 'redux-thunk';
import createHistory from 'history/createBrowserHistory'
import createRavenMiddleware from "raven-for-redux";
// Create a history of your choosing (we're using a browser history in this case)
export const history = createHistory({
    basename: '/tempApp/',
  })

// Build the middleware for intercepting and dispatching navigation actions
// const middleware = routerMiddleware(history);
//config middlewars
const middlewares = [thunk, routerMiddleware(history), createRavenMiddleware(window.Raven, {
    // Optionally pass some options here.
})];

//Store enhancer 是一个组合 store creator 的高阶函数，返回一个新的强化过的 store creator。这与 middleware 相似，它也允许你通过复合函数改变 store 接口
//compose 就提供了这种高阶函数逻辑的用法，从参数右到左来组合多个函数，传入当前store实例中
const composeEnhancers = process.env.__DEV__ ? (window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose) : compose;

const persisStoreConfig = {
    whitelist: [
        'tokenReducer',
        'router',
    ],
    transforms: [
        immutableTransform({
            records: [
                TokenRecord,
            ]
        }),
    ],
}

export default function () {
    const store = createStore(
        reducers,
        composeEnhancers(applyMiddleware(...middlewares), autoRehydrate()),
    );
    persistStore(store, persisStoreConfig);
    return store
}


