//@flow

import {
    StartCD,
    CancleCD,
    CountCD,
} from './CDManangerActionType'

import * as CDKeyConfig from './CDKeyConfig'

// import {
//     GetPhoneVCodeOpt
// } from '../../common/HFNetworkOPTs'

export function startCD(cdKey: string) {
    return {
        type: StartCD,
        cdKey,
        totalSecond: CDKeyConfig[cdKey]
    }
}

export function cancleCD(cdKey: string) {
    return {
        type: CancleCD,
        cdKey,
    }
}

export function countCD(cdKey: string) {
    return {
        type: CountCD,
        cdKey,
    }
}


